<template>
	<view class="box">
		<web-view :src="url"  v-if="url.length>0" :webview-styles="webviewStyles"></web-view>
	</view>
</template>

<script>
	let seqId = "";
	let offerRewardSeqId = "";
	export default {
		data() {
			return {
				url:"",
				webviewStyles: {
					progress: {
						color: '#f16a6a'
					}
				}
			}
		},
		onLoad(option) {
			seqId = option.seqId;//文章id
			offerRewardSeqId = option.offerRewardSeqId;//悬赏id
			let customerSeqId=this.$common.getLocalSync('customerSeqId')
			let token=this.$common.getLocalSync('token');
			if(seqId){
				// 编辑文章
				this.url=this.$webconfig.web_url+'/editor?seqId='+seqId+'&offerRewardSeqId='+offerRewardSeqId+'&token='+token+'&customerSeqId='+customerSeqId
			}else {
				// 重新制作文章
				this.url=this.$webconfig.web_url+'/editor?token='+token+'&customerSeqId='+customerSeqId
			}
		}
	}
</script>

